body {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.wrap-main {
  display: flex;
  height: 100%; }

.side-panel {
  width: 23.7%; }
  .side-panel .top-logo {
    height: 71px;
    background: #D1D4D9; }
    .side-panel .top-logo .logo-img {
      background: url(../img/logo_black.svg) no-repeat;
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: 80%;
      display: inline-block; }
  .side-panel .navigation-city {
    background: #033953;
    padding: 26px 10px 45px 131px; }
    .side-panel .navigation-city .city {
      display: block;
