.menu-mobile {
	padding: 56px 43px 220px 49px;
	position: absolute;
	top: 0;
	left: 0;
	display: none;
}

.close-menu-mobile {
	content: "";
	position: absolute;
	top: 20px;
	right: 19px;
	width: 14.38px;
	height: 14.39px;
}

.menu-mobile-list {
	margin: 0;
	padding: 0;
	list-style: none;

	.menu-mobile-item {
		position: relative;
		margin-bottom: 42px;

		&:after {
			content: "";
			position: absolute;
			border-bottom: 1px solid rgba(0, 0, 0, 0.15);
			width: 39px;
			bottom: -20px;
			left: 3px;
		}
		
		.menu-mobile-link {
			text-decoration: none;
			font-family: Gotham Pro;
			line-height: normal;
			font-size: 16px;	
			color: #314A5D;
			font-weight: 500;
		}
	}
}

.exit-personal {
	position: relative;
	display: inline-block;
	text-decoration: none;
	font-family: Gotham Pro;
	line-height: normal;
	font-size: 16px;	
	color: #314A5D;
	font-weight: 500;
	margin-bottom: 33px;
	margin-left: 20px;

	&:before {
		content: "";
		position: absolute;
		background: url(../img/exit-personal.svg) no-repeat;
		width: 16px;
		height: 16px;
		left: -20px;
    top: 2px;
	}

	&:after {
		content: "";
		position: absolute;
		border-bottom: 1px solid rgba(0, 0, 0, 0.15);
		width: 320px;
		left: -20px;
		bottom: -33px;
	}
}

.block-connection {
	display: flex;
	margin-top: 33px;	
	flex-direction: column;
	justify-content: space-between;
} 

.city-phone {
	position: relative;
	display: flex;
	justify-content: space-between;
	margin-bottom: 20px;

	&:first-child:before {
		content: "";
		position: absolute;
		background: url(../img/moscow-mobile-menu.svg) no-repeat;
		width: 100%;
		height: 100%;
		top: 8px;
		left: 0px;
	}

	&:nth-child(2):before {
		content: "";
		position: absolute;
		background: url(../img/spb-mobile-menu.svg) no-repeat;
		width: 100%;
		height: 100%;
		top: 8px;
		left: 0px;
	}

	&:last-child:before {
		content: "";
		position: absolute;
		background: url(../img/saratov-mobile-menu.svg) no-repeat;
		width: 100%;
		height: 100%;
		top: 8px;
		left: 0px;
	}

	&:last-child {
		margin-bottom: 0;
	}
}

.connection-text {
	position: relative;
	display: block;
	font-family: Gotham Pro;
	line-height: 23px;
	font-size: 16px;
	font-weight: normal;
	color: #314A5D;
	margin-left: 51px;

	.city-phone-number-link {
		text-decoration: none;
		display: block;
		font-family: Gotham Pro;
		line-height: 23px;
		font-size: 16px;
		color: #314A5D;
	}
}	

.connection-button {
  margin-left: 36px;
	margin-top: 14px;
	
	.connection-button-link {
		text-decoration: none;
		padding: 8px 27px 8px 26px;
		border: 1px solid #779FDB;
		font-family: Gotham Pro;
		line-height: 15px;
		font-size: 16px;	
		font-weight: normal;	
		color: #779FDB;
	}

	.social-button-link {
		text-decoration: none;
		padding: 8px 32px 8px 31px;
		border: 1px solid #779FDB;
		font-family: Gotham Pro;
		line-height: 15px;
		font-size: 16px;	
		font-weight: normal;	
		color: #779FDB;
	}
}

.social-write {
	position: relative;
	display: flex;
	justify-content: space-between;
	margin-bottom: 20px;

	&:last-child {
		margin-bottom: 0;
	}

	&:first-child:before {
		content: "";
		position: absolute;
		background: url(../img/whats-app.svg) no-repeat;
		width: 100%;
		height: 100%;
		top: 8px;
		left: 0;
	}

	&:nth-child(2):before {
		content: "";
		position: absolute;
		background: url(../img/viber.svg) no-repeat;
		width: 100%;
		height: 100%;
		top: 8px;
		left: 0;
	}

	&:last-child:before {
		content: "";
		position: absolute;
		background: url(../img/telegram.svg) no-repeat;
		width: 100%;
		height: 100%;
		top: 8px;
		left: 0;
	}

	.social-write-title-text {
		display: inline-block;
		padding-top: 7px;
	}
}

.button-main-menu-mobile {
	position: relative;
	display: inline-block;
	margin-top: 41px;
	text-decoration: none;
	font-family: Gotham Pro;
	line-height: normal;
	font-size: 16px;
	font-weight: bold;
	color: #314A5D;
	margin-left: 24px;

	&:before {
		content: "";
		position: absolute;
		background: url(../img/arrow-main-menu.svg) no-repeat;
		width: 100%;
		height: 100%;
    top: 7px;
    left: -21px;
	}

	&:after  {
		content: "";
		position: absolute;
		border-bottom: 1px solid rgba(0, 0, 0, 0.15);
		width: 39px;
		left: -21px;
		bottom: -15px;
	}
}

.main-menu-mobile {
	margin-top: 20px;
	list-style: none;

	.main-menu-mobile-item {
		position: relative;
		font-family: Gotham Pro;
		line-height: normal;
		font-size: 16px;	
		font-weight: 500;
		margin-bottom: 20px;
		margin-left: 40px;

		&:last-child:before {
			content: "";
			position: absolute;
			background: url(../img/arrow-main-menu.svg) no-repeat;
			width: 100%;
			height: 100%;
    	top: 7px;
    	right: -135px;
		}

		&:after {
			content: "";
			position: absolute;
			border-bottom: 1px solid rgba(0, 0, 0, 0.15);
			width: 39px;
			left: 0;
			bottom: -15px;

		}

		.main-menu-mobile-link {
			text-decoration: none;
			color: #314A5D;
		}
	}
}


